import {useState} from 'react'
import {Dropdown, DropdownButton, Button, Form} from 'react-bootstrap'

const MarketsInfo = ({market, marketsInfo, onChangeMarket, onMint, onApprove, onAddLiquidity, onRemoveLiquidity, onAddMargin, onRemoveMargin}) => {

    const [amount, setAmount] = useState('')

    const info = marketsInfo[market] || {}

    return (
        <div>
            <DropdownButton id="dropdown-basic-button" title={market} onSelect={(e) => onChangeMarket(e)}>
                {Object.values(marketsInfo).map((m) => (
                    <Dropdown.Item active={m.balanceLp || m.balanceTd} key={m.assetSymbol} eventKey={m.assetSymbol}>{m.assetSymbol}</Dropdown.Item>
                ))}
            </DropdownButton>
            <table>
            <tbody>
                <tr>
                    <th>BToken</th>
                    <th>Address</th>
                    <th>Price</th>
                    <th>Balance in Wallet</th>
                    <th>Balance in Lp Vault</th>
                    <th>Balance in Td Vault</th>
                </tr>
                <tr>
                    <td>{info.assetSymbol}</td>
                    <td>{info.assetAddress}</td>
                    <td>{info.price}</td>
                    <td>{info.balance || '---'}</td>
                    <td>{info.balanceLp || '---'}</td>
                    <td>{info.balanceTd || '---'}</td>
                </tr>
            </tbody>
            </table>
            <br/>
            <Button disabled={info.assetSymbol === 'ETH'} onClick={() => onMint(info.assetSymbol)}>Mint</Button>
            <Button disabled={info.approved || info.assetSymbol === 'ETH'} onClick={() => onApprove(info.assetSymbol)}>Approve</Button>
            <Form.Control value={amount} onChange={(e) => {setAmount(e.target.value)}} />
            <Button onClick={() => onAddLiquidity(info.assetSymbol, amount)}>Add Liquidity</Button>
            <Button onClick={() => onRemoveLiquidity(info.assetSymbol, amount)}>Remove Liquidity</Button>
            <Button onClick={() => onAddMargin(info.assetSymbol, amount)}>Add Margine</Button>
            <Button onClick={() => onRemoveMargin(info.assetSymbol, amount)}>Remove Margin</Button>
        </div>
    )

}

export default MarketsInfo
